const { ccclass, property } = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property({type: cc.Node , tooltip: "Reference to the node"})
    spriteNode: cc.Node = null;

    timeElapsed: number = 0;

    @property(Number)
    resetTime: number = 20;

    start () {
        cc.log("start");
        // Ensure spriteNode is set and apply tween directly to it
        if (this.spriteNode) {
            let scaleInOut = cc.tween(this.spriteNode)
                .to(2, { scale: 5 })
                .repeatForever(); 
                console.log("ok"); // Creates an infinite loop

            scaleInOut.start();
        } else {
            cc.warn("spriteNode is not assigned!");
        }
    }

   
}
